body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.container {
  color: #1d222b;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  background: #888;
}

.container h1 {
  font-size: 2.6rem;
  margin-bottom: 0.2rem;
}

.container .labels {
  list-style: none;
  display: flex;
  text-transform: uppercase;
  font-size: 0.8rem;
  padding: 0.3rem 0.3rem;
  background-color: #1d222b;
  border-radius: 1.5rem;
}

.active-label {
  color: #c9ccea;
  background-color: #790e43;
}

.timer-container {
  display: flex;
  flex: 2;
  align-items: center;
  justify-content: center;
}

.time-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 15rem;
  width: 15rem;
  border-radius: 7.5rem;
  color: #efefef;
  background: #1d222b;
  font-size: 3.5rem;
  box-shadow: 1rem 1.5rem 2rem rgba(0, 0, 0, 0.6);
}

.button-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 2rem;
}

button {
  color: #efefef;
  font-size: 0.8rem;
  padding: 0.5rem 1.2rem;
  border: none;
  border-radius: 3rem;
  margin: 0.2rem;
  background: #1d222b;
  min-width: 80px;
  cursor: pointer;
}

.button-wrapper button.active {
  color: #c9ccea;
  background-color: #1d222b;
  border: 1px solid #1d222b;
}
.button-wrapper button {
  background-color: #790e43;
}
.form-container {
  text-align: center;
}

.input {
  overflow: hidden;
}

.input-wrapper {
  margin: 1.7rem;
}
.input-wrapper input {
  height: 40px;
  width: 40px;
  padding: 2rem;
  background-color: #1d222b;
  color: #c9ccea;
  border: none;
  border-radius: 50%;
  margin-right: 0.3rem;
  text-align: center;
  font-size: 13pt;
}
